.Scorebox {
  position: relative;
  padding: 5px;

  background-color: #bbada0;
  border-radius: 3px;
  text-align: center;
  color: #fff;
}

.Scorebox--large {
  font-size: 2.5rem;
  padding-left: 0.5em;
  padding-right: 0.5em;

  color: #fff;
  background-color: rgb(216, 136, 80);
}

.Scorebox__delta {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

  font-weight: bold;
  color: green;
  -webkit-animation: delta 1s ease-out;
          animation: delta 1s ease-out;
  opacity: 0;
}

.Scorebox__label {
  color: #eee4da;
  font-size: 0.5em;
  text-transform: uppercase;
  white-space: nowrap;
}

.Scorebox__score {
  font-weight: bold;
}

@-webkit-keyframes delta {
  0% {
    opacity: 0;
    margin-top: 0;
  }

  25% {
    opacity: 0.25;
  }

  50% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    margin-top: 50px;
    opacity: 0;
  }
}

@keyframes delta {
  0% {
    opacity: 0;
    margin-top: 0;
  }

  25% {
    opacity: 0.25;
  }

  50% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    margin-top: 50px;
    opacity: 0;
  }
}



