* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: auto;
  padding: 30px 20px;
  max-width: 500px;

  font-family: sans-serif;
  font-size: 18px;
  color: #776e65;
  background-color: #faf8ef;
}

@media (min-width: 450px) {
  body {
    padding: 100px 10px 40px;
  }
}

a, a:hover, a:visited {
  font-weight: bold;
  color: #776e65;
}

p {
  line-height: 1.65em;
  margin: 30px 0;
}

p:last-of-type {
  margin-bottom: 0;
}

hr {
  margin: 30px 0;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: #d8d4d0;
}



